    display: inline-block;
    width: 8px;
    height: 8px;
    margin: 0 10px;
    border-radius: 50%;
    background: #52c41a;
}
.box {
    margin: 20px;
}
.lf {
    width: 20%;
    height: auto;
    max-height: 600px;
    overflow: auto;
    border-radius: 4px;
    border: 1px solid #cfd0dd;
    padding: 20px;
    font-size: 14px;
    font-family: PingFangSC-Medium, PingFang SC;
    .title {
        font-weight: 500;
        color: #333333;
        cursor: pointer;
    }
    .list-item {
        margin-top: 20px;

        .list-title {
            font-weight: 400;
            color: #999999;
        }
        font-weight: 400;
        color: #333333;
        .item {
            padding: 15px;
            &:hover {
                background: #f6f6f6;
                border-radius: 2px;
            }
            &.active {
                background: #f6f6f6;
                border-radius: 2px;
            }
        }
    }
}
.rg {
    margin: 0 20px;
    flex: 1;
    border-radius: 4px;

    height: auto;
    overflow: auto;
    overflow: auto;
    border: 1px solid #cfd0dd;
}
.role-box {
    font-size: 14px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: #666666;
    .tips {
        padding-left: 12px;
        height: 40px;
        line-height: 40px;
        background: #edf5ff;
        border: 1px solid #97c3ff;
        border-radius: 4px;
        margin-bottom: 20px;
    }
}
.check-box {
    .list {
        margin: 20px 0;
        .list-title {
            width: 90px;
            text-align: right;
            margin-right: 50px;
        }
        .check {
            // width: 90px;
            // text-align: right;
            // display: flex;
        }
    }
}
